body {
	margin: 0;
	padding: 0;
	font-family: "Courier New", Courier, monospace;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
	background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

button {
	cursor: pointer;
}

.Tilt {
	background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
	background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
}

.form {
	width: 700px;
	background: -webkit-radial-gradient(0% 50%, circle farthest-side, #fb1 23.5%, rgba(240, 166, 17, 0) 0)21px 30px,
		-webkit-radial-gradient(0% 50%, circle farthest-side, #b71 24%, rgba(240, 166, 17, 0) 0)19px 30px,
		-webkit-linear-gradient(#fb1 14%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 85%, #fb1 0)0 0,
		-webkit-linear-gradient(
				300deg,
				#fb1 24%,
				#b71 0,
				#b71 26%,
				rgba(240, 166, 17, 0) 0,
				rgba(240, 166, 17, 0) 74%,
				#b71 0,
				#b71 76%,
				#fb1 0
			)0 0,
		-webkit-linear-gradient(
				60deg,
				#fb1 24%,
				#b71 0,
				#b71 26%,
				rgba(240, 166, 17, 0) 0,
				rgba(240, 166, 17, 0) 74%,
				#b71 0,
				#b71 76%,
				#fb1 0
			)0 0,
		-webkit-linear-gradient(left, #b71 2%, #fb1 0, #fb1 98%, #b71 0%)0 0 #fb1;
	background: radial-gradient(circle farthest-side at 0% 50%, #fb1 23.5%, rgba(240, 166, 17, 0) 0)21px 30px,
		radial-gradient(circle farthest-side at 0% 50%, #b71 24%, rgba(240, 166, 17, 0) 0)19px 30px,
		linear-gradient(#fb1 14%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 85%, #fb1 0)0 0,
		linear-gradient(
				150deg,
				#fb1 24%,
				#b71 0,
				#b71 26%,
				rgba(240, 166, 17, 0) 0,
				rgba(240, 166, 17, 0) 74%,
				#b71 0,
				#b71 76%,
				#fb1 0
			)0 0,
		linear-gradient(
				30deg,
				#fb1 24%,
				#b71 0,
				#b71 26%,
				rgba(240, 166, 17, 0) 0,
				rgba(240, 166, 17, 0) 74%,
				#b71 0,
				#b71 76%,
				#fb1 0
			)0 0,
		linear-gradient(90deg, #b71 2%, #fb1 0, #fb1 98%, #b71 0%)0 0 #fb1;
	background-size: 40px 60px;
}

.bounding-box {
	position: absolute;
	box-shadow: inset 0 0 0 3px #149df2;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
}

.App {
	text-align: center;
}

.center {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.particles {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
}

.wrapper {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.profile {
  width: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.profile h1 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.table tbody {
  border: 1px solid black;
  text-align: left;
}

.table tbody th,
.table tbody td {
  padding: 3px 0;
}

.profile button {
  margin: 35px 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

